import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import { useTranslations, useIntl } from "next-intl";
import React from "react";
import { parse } from "tinyduration";
import FlightTakeoff from "@mui/icons-material/FlightTakeoff";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import FlightLandIcon from "@mui/icons-material/FlightLand";
import Divider from "@mui/material/Divider";
// import { useTheme } from "../theme";
import Chip from "@mui/material/Chip";
import Image from "next/image";

const DotSeperator = () => (
  <Box
    sx={{
      mx: 1,
      display: "inline-block",
    }}
  >
    <Box
      sx={{
        "&::after": {
          content: '"Â·"',
        },
      }}
    ></Box>
  </Box>
);

export const FlightTicketBody = ({ ticket }: any) => {
  const t = useTranslations();
  // const theme = useTheme();
  const intl = useIntl();

  return (
    <Box
      sx={{
        margin: -2,
        padding: 2,
        mt: 2,
        borderBottomLeftRadius: 3,
        borderBottomRightRadius: 3,
      }}
    >
      <Stack spacing={1}>
        {ticket.slices.map((slice: any, index: number) => {
          return (
            <Paper key={slice.id} variant="outlined">
              <Box sx={{ p: 2 }}>
                <Typography
                  sx={{
                    fontSize: { xs: 14, sm: 16 },
                    fontWeight: "bold",
                  }}
                >
                  {/* {slices.length === is end display arrival} */}
                  {ticket.slices.length - 1 === index ? (
                    <span>{t("flights-return")}</span>
                  ) : (
                    <span>{t("flights-departure")}</span>
                  )}
                  <DotSeperator />
                  <span>
                    {intl.formatDateTime(
                      new Date(slice.segments[0].departing_at),
                      {
                        month: "short",
                        day: "numeric",
                        weekday: "short",
                      }
                    )}
                  </span>
                </Typography>
                <Typography
                  sx={{ fontSize: { xs: 12, sm: 14 } }}
                  variant="body2"
                >
                  {`${parse(slice.duration).hours || "00"}h ${
                    parse(slice.duration).minutes || "00"
                  }m`}
                  <DotSeperator />
                  {t("search-results-flight-details-stops", {
                    value: slice.segments.length - 1,
                    value2: slice.segments.length - 1,
                  })}
                </Typography>
                <Divider sx={{ my: 1 }} />
                {slice.segments.map((flight: any, i: number) => {
                  return (
                    <Box key={i}>
                      <Box
                        component="ul"
                        sx={{ fontSize: 14, listStyle: "none", padding: 0 }}
                      >
                        <Box component="li" sx={{ mb: 2 }}>
                          <Box>
                            <FlightTakeoff
                              sx={{
                                display: {
                                  xs: "none",
                                  sm: "inline-block",
                                },
                                mr: 2,
                                verticalAlign: "middle",
                              }}
                            />
                            <strong>
                              {intl.formatDateTime(
                                new Date(flight.departing_at),
                                {
                                  hour: "numeric",
                                  minute: "numeric",
                                  hourCycle: "h23",
                                }
                              )}
                            </strong>
                            <DotSeperator />
                            {flight.origin.name}
                            <strong>
                              {` `}({flight.origin.iata_code})
                            </strong>
                          </Box>
                        </Box>
                        <Box
                          component="li"
                          sx={{
                            mb: 2,
                            paddingLeft: "28px",
                            marginLeft: "12px",
                            borderLeft: "1px solid grey",
                          }}
                        >
                          <Chip
                            label={`${parse(flight.duration).hours || "00"}h ${
                              parse(flight.duration).minutes || "00"
                            }m`}
                          />
                          <DotSeperator />
                          <Box
                            sx={{
                              verticalAlign: "middle",
                              display: "inline-block",
                            }}
                          >
                            <Box
                              sx={{
                                verticalAlign: "middle",
                                display: "inline-block",
                              }}
                            >
                              <Image
                                key={flight.operating_carrier.name}
                                src={`https://images.kiwi.com/airlines/32/${flight.operating_carrier.iata_code}.png?default=airline.png`}
                                alt={flight.operating_carrier.name}
                                width={24}
                                height={24}
                              />
                            </Box>
                          </Box>
                          <Box component="span" sx={{ ml: 1 }}>
                            {flight.operating_carrier.name}
                          </Box>
                          <DotSeperator />
                          <span>
                            {t("search-results-flight-details-class", {
                              value:
                                flight.fare_brand_name === "Economy"
                                  ? "Y"
                                  : "C",
                            })}
                          </span>
                          {flight.aircraft && (
                            <>
                              <DotSeperator />
                              <span>{flight.aircraft.name}</span>
                            </>
                          )}
                          <DotSeperator />
                          <span>
                            {flight.operating_carrier.iata_code}{" "}
                            {flight.marketing_carrier_flight_number}
                          </span>
                        </Box>
                        <Box component="li" sx={{ mb: 2 }}>
                          <Box>
                            <FlightLandIcon
                              sx={{
                                display: {
                                  xs: "none",
                                  sm: "inline-block",
                                },
                                mr: 2,
                                verticalAlign: "middle",
                              }}
                            />
                            <strong>
                              {intl.formatDateTime(
                                new Date(flight.arriving_at),
                                {
                                  hour: "numeric",
                                  minute: "numeric",
                                  hourCycle: "h23",
                                }
                              )}
                            </strong>
                            <DotSeperator />
                            {`${flight.destination.name} `}
                            <strong>({flight.destination.iata_code})</strong>
                          </Box>
                        </Box>
                        {slice.segments[i + 1] && slice.segments.length !== i && (
                          <Box
                            sx={{
                              my: 4,
                            }}
                          >
                            <AccessTimeIcon
                              sx={{ mr: 2, verticalAlign: "middle" }}
                            />
                            <>
                              <span>
                                <strong>
                                  {`${
                                    parse(slice.segments[i + 1].duration).hours || "00"
                                  }h ${
                                    parse(slice.segments[i + 1].duration)
                                      .minutes || "00"
                                  }m`}
                                </strong>
                              </span>
                              <DotSeperator />
                              {t("search-results-flight-details-layover")}
                              <DotSeperator />
                              <span>
                                {slice.segments[i + 1].origin.city_name}{" "}
                                <strong>
                                  ({slice.segments[i + 1].origin.iata_code})
                                </strong>
                              </span>
                            </>
                          </Box>
                        )}
                      </Box>
                    </Box>
                  );
                })}
              </Box>
            </Paper>
          );
        })}
      </Stack>
    </Box>
  );
};
